@font-face {
    font-family: Din;
    src: url(/fonts/DIN\ Medium.ttf);
}

@font-face {
    font-family: light;
    src: url(/fonts/DIN.ttf);
}

body{
    margin: 0;
    padding: 0;
    color: black;
    background-color: #FEFAF6;
}

//darkmode 
.darkmode{
    background-color: #22092C;
    color: white;
}

.darkmode a{
    color: white;
}

//navbar css
.navbar img{
  width: 5%;
}

.navbar{
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    justify-content: space-around;
    font-family: Din;
    font-size: large;
    margin-top: 2%;
}

.navbar nav{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    width: 50%;
}

.navbar nav a{
    text-decoration: none;
    color: black;
}


.navbar .icons{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 6%;
}

//css for out datahub
.ourdata {
    margin-top: 10%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 100%;
    align-items: center;
}

.data_1, .data_2 {
    width: 40%; /* Adjust widths to create space between them */
}

.data_1 {
    display: flex;
    flex-direction: column;
    align-content: space-evenly;
}

.header {
    width: 100%;
    font-size: larger;
    margin-bottom: 20px; /* Add some bottom margin for spacing between headers */
}

.header_text {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    justify-content: space-between;
}

.header_text p {
    font-family: light;
}

.header_text h2 {
    font-family: Din;
}

.header_text span {
    color: orangered;
}

.line {
    background-color: lightblue;
    color: #FEFAF6;
    border-radius: 20px;
    position: relative;
    overflow: hidden;
}

.inside_line {
    width: 0%;
    height: 25px;
    background-color: orangered;
    border-radius: 20px;
    font-family: Din;
    transition: width 2s;
    position: relative;
}

.inside_line span {
    position: relative;
    margin-left: 10%;
}

.numbers {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-family: light;
}

.yearly {
    background-color: #22092C;
    color: #FEFAF6;
    padding: 6px;
    border-radius: 25px;
    padding-left: 5%;
    padding-right: 5%;
    border: orangered solid 3px;
    transition: background-color 1s ease-in-out;
}

.yearly:hover {
    background-color: orangered;
}

.data_2 {
    display: flex;
    flex-direction: row; /* Adjust flex direction for better layout */
    justify-content: space-between;
    padding-left: 30px;
    padding-right: 30px;
}

.side_1 {
    width: 50%;
    display: flex;
    flex-direction: column;
}

.side_1 p{
    font-family: light;
}

.piechart {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background-image: conic-gradient(
        beige 100deg,
        orangered 0 235deg
    );
    animation: fillPie 3s linear forwards;
}

.piechart-text-principle{
    position: absolute;
    margin-top: 6%;
    margin-left: 2%;
    font-family: Din;
}

.piechart-text-interest{
    position: absolute;
    margin-top: 4.5%;
    margin-left: 8%;
    font-family: Din;
}

@keyframes fillPie {
    0% {
        background-image: conic-gradient(
            beige 0deg,
            orangered 0deg
        );
    }
    10% {
        background-image: conic-gradient(
            beige 10deg,
            orangered 0 10deg
        );
    }
    20% {
        background-image: conic-gradient(
            beige 20deg,
            orangered 0 20deg
        );
    }
    30% {
        background-image: conic-gradient(
            beige 30deg,
            orangered 0 30deg
        );
    }
    40% {
        background-image: conic-gradient(
            beige 40deg,
            orangered 0 40deg
        );
    }
    50% {
        background-image: conic-gradient(
            beige 50deg,
            orangered 0 50deg
        );
    }
    60% {
        background-image: conic-gradient(
            beige 60deg,
            orangered 0 60deg
        );
    }
    70% {
        background-image: conic-gradient(
            beige 70deg,
            orangered 0 70deg
        );
    }
    80% {
        background-image: conic-gradient(
            beige 80deg,
            orangered 0 80deg
        );
    }
    90% {
        background-image: conic-gradient(
            beige 90deg,
            orangered 0 90deg
        );
    }
    100% {
        background-image: conic-gradient(
            beige 100deg,
            orangered 0 235deg
        );
    }
}

.calculation{
    padding-left: 3%;
    padding-right: 3%;
}

.calculation h1{
  font-family: Din;
  margin-left: 45%;
}

.principal{
    color: orangered;
}

.interest{
    color: white;
}

.calculation p{
    font-family: light;
    font-size: larger;
}

.calculation span{
    color: orangered;
}